<!-- by 添少 -->
<template>
  <section>
    <topbar>
      <template slot="before">
        <b class="iconfont icon-back" @click="$router.go(-1)"></b>
      </template>
      俱乐部详情
    </topbar>
    <section class="preview">
      <div class="cover">
        <img class="db fit-cover" :src="detail.mainimg"/>
      </div>
      <div class="text">
        <h3>{{detail.name}}</h3>
        <p>{{detail.desc}}</p>
      </div>
      <div class="ft">
        <template v-if="detail.joinSatus===1">
          <button class="ui-button primary" @click="handJoin">加入俱乐部</button>
        </template>
        <template v-else>
          <button class="ui-button disabled">已加入俱乐部</button>
        </template>
      </div>
    </section>
  </section>
</template>

<script>
  import { mapState } from 'vuex'
  import Topbar from '@/components/topbar'

  export default {
    name: 'club',
    components: {
      Topbar,
    },
    data () {
      return {
        detail: {},
      }
    },
    computed: {
      ...mapState({}),
      tabList () {
        let list = []
        this.tab.list.forEach(e => list.push(e.label))
        return list
      }
    },
    created () {
      this.init()
    },
    methods: {
      init () {
        if (this.$route.query.clubId) {
          this.$http.get(this.$port.GET_CLUB_DETAIL, {
            params: {
              clubId: this.$route.query.clubId
            }
          }).then(res => {
            if (res.code === 200) {
              this.detail = res.data
            }
          })
        }
      },
    }
  }
</script>

<style lang="scss" scoped>
  .ui-button {
    background-color: #fff;
    padding: 6px 10px;
    border-radius: 10px;
    border-width: 0;
    &.primary {
      background-color: #1797c6;
      color: #fff;
    }
    &.disabled {
      background-color: #ccc;
      color: #fff;
    }
  }
  .preview {
    background-color: #fff;
    color: #333;
    margin-bottom: 10px;
    .text {
      padding: 0 15px;
      margin-bottom: 10px;
    }
    .cover {
    }

    h3 {
      font-size: 22px;
      font-weight: 600;
      padding-top: 5px;
    }
    .ft {
      text-align: center;
      padding-bottom: 15px;
    }
  }
</style>



